import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components:{
	       wPicker
	   },
	data() {
		return {
			title:"",
			content:"",
			startTime:"",
			endTime:"",
			hasclickback:false
		}
	},
	methods: {
		getRate(e){
			const that = this;
			that.rate=e.value
		},
		bindTextAreaBlur1(e){
			this.title=e.detail.value;
		},
		bindTextAreaBlur2(e){
			this.content=e.detail.value;
		},
		datePicker1: function(e) {
			const that = this;
			that.$refs.date1.show();
		},
		onConfirm1(val){
			const that= this;
			that.startTime=val.obj.year+'-'+val.obj.month+'-'+val.obj.day+' '+val.obj.hour+':'+val.obj.minute+':'+val.obj.second;
		},
		datePicker2: function(e) {
			const that = this;
			that.$refs.date2.show();
		},
		onConfirm2(val){
			const that= this;
			that.endTime=val.obj.year+'-'+val.obj.month+'-'+val.obj.day+' '+val.obj.hour+':'+val.obj.minute+':'+val.obj.second;
		},
		sub(){
			const that = this;
			setTimeout(()=>{
				if(that.title==""||that.content==""||that.startTime==""||that.endTime==""){
					uni.showToast({
						title:"请输入必填内容",
						icon:"none"
					})
				}
				else if(new Date(that.startTime)>=new Date(that.endTime)){
					uni.showToast({
						title:"结束时间必须大于开始时间",
						icon:"none"
					})
				}
				else{
					that.subAdd();
					
				}
			},100)
		},
		subAdd(){
			const that = this;
			
			let param = {
				title: that.title,
				content: that.content,
				startTime:that.startTime,
				endTime:that.endTime
			}
			that.$api.noticeAdd(param).then(res => {
				if (res.success) {
					uni.showToast({
						title:"发布成功"
					})
					setTimeout(()=>{
						that.$Router.back(1);
					},1000)	
					var pages = getCurrentPages(); //当前页
					var beforePage = pages[pages.length - 2].route; //上个页面
					if(beforePage=="pages/my/manageNavs/notice/list"){
						that.prevPageReload();
					}	
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
			
		},
		prevPageReload(){
			var pages = getCurrentPages(); //当前页
			var beforePage = pages[pages.length - 2]; //上个页面




			beforePage.onLoad()

		},
		back(){
			const that = this;
			if(that.title!=""||that.content!=""||that.startTime!=""||that.endTime!=""){	
				uni.showModal({
						content: "退出后将不保留已编辑的内容，确认退出编辑？",
						confirmText: "继续编辑",
						cancelText: "退出",
					success: function (res) {
						if (res.confirm) {
				
						} else if (res.cancel) {
							that.$Router.back(1);
						}	
					}
				});		
			}
			else{
				that.$Router.back(1);
			}		
		}
	},
	
}
